.blog_aside {
	align-items: stretch;
	background: lighten($pri, 5%);
	border: 1px solid $sec;
	border-radius: 4px;
	box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.35);
	color: $white;
	display: flex;
	flex-flow: column nowrap;
	height: 95vh;
	justify-content: flex-start;
	min-width: 220px;
	max-width: 320px;
	&-last_articles {
		border: 1px soli crimson;
		background: $white;
		width: 320px;
		overflow: hidden;

		& img {
			height: 50px;
		}
	}
	&-form_group {
		align-items: center;
		display: flex;
		flex-flow: row;
		justify-content: space-between;
		width: 100%;
		& input {
			border: 0;
			border-right: 0;
			border-top: 1px solid lighten($pri, 45%);
			border-bottom: 1px solid lighten($pri, 45%);
			border-left: 1px solid lighten($pri, 45%);
			border-radius: 2px 0 0 2px;
			color: lighten($sec, 15%);
			display: block;
			font-size: 1rem;
			height: 32px;
			margin-bottom: 10px;
			padding: 10px;
			transition: 0.5s ease;
			width: 100%;
			&:focus {
				background: lighten($pri, 63%);
				border: 0;
				border-left: 7px solid lighten($pri, 45%);
				border-bottom: 1px solid lighten($pri, 45%);
				border-top: 1px solid lighten($pri, 45%);
				border-radius: 4px 0 0 4px;
			}
		}
		& i {
			background: $white;
			border: 0;
			border-left: 0;
			border-bottom: 1px solid lighten($pri, 45%);
			border-right: 1px solid lighten($pri, 45%);
			border-radius: 0 2px 2px 0;
			border-top: 1px solid lighten($pri, 45%);
			color: lighten($pri, 15%);
			cursor: pointer;
			font-size: 1.2rem;
			height: 32px;
			margin-bottom: 10px;
			padding: 5px;
			transition: 0.5s ease;
			width: 40px;
		}
	}
	&-navbar {
		align-items: center;
		color: $white;
		margin-top: 10px;
		display: flex;
		padding: 5px;
		width: 100%;
		justify-content: space-between;
		& h3 {
			font-weight: lighter;
		}
		& span {
			font-size: 1.5rem;
			margin-left: 5px;
		}
	}
	&-new_entry {
		align-items: center;
		color: $white;
		cursor: pointer;
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 100%;

		transition: 0.3s;
		&:hover {
			background: darken($pri, 5%);
			color: darken($white, 15%);
		}
	}
}

.blog_card {
	&-body {
		& a {
			display: block;
			color: $pri;
			font-family: 'Roboto', sans-serif;
			font-size: 0.8rem;
			font-weight: 600;
			padding: 1rem;
			transition: all 0.3s;
			&:hover {
				cursor: pointer;
				text-decoration: underline;
				font-weight: 600;
			}
		}
		& p {
			color: lighten($sec, 5%);
			font-family: 'Roboto', sans-serif;
			font-size: 1.1rem;
			font-weight: 400;
			margin-bottom: 0.5rem;
			letter-spacing: 1px;
			padding: 20px 20px 10px 20px;
			text-indent: 35px;
		}
		& ul {
			margin: 20px;
		}
		& li {
			color: lighten($sec, 5%);
			font-family: 'Roboto', sans-serif;
			font-size: 1.1rem;
			font-weight: 400;
			margin-bottom: 0.5rem;
			letter-spacing: 1px;
			list-style: inside;
			margin: 0 20px;
			text-indent: 35px;
		}
		& .bold {
			font-weight: bold;
		}
		& .underline {
			border-bottom: 2px solid $pri;
		}
	}
	&-category {
		background: #23548a;
		border-radius: 16px 16px 0 0;
		color: #fff;
		cursor: pointer;
		display: block;
		left: 0px;
		top: 5px;
		padding: 5px 10px 6px 15px;
		position: relative;
		width: 12rem;
		z-index: 100;
		i {
			margin-right: 5px;
			animation: bouncing 1.2s ease-in alternate infinite;
		}
		@keyframes bouncing {
			0% {
				transform: translateX(0%);
			}
			80% {
				transform: translateX(0%);
			}
			90% {
				transform: translateX(10%);
			}
			100% {
				transform: translateX(0%);
			}
		}
	}
	&-container {
		margin-bottom: 40px;
		& img {
			border: 0;
			border-top: 2px solid #23548a;
			border-left: 2px solid #23548a;
			border-right: 2px solid #23548a;
			border-radius: 0 12px 0 0;
			height: 35vh;
			left: 0;
			object-fit: cover;
			overflow: hidden;
			position: relative;
			top: 5px;
			width: 100%;
			z-index: 101;
		}
	}
	&-date {
		background: #23548a;
		align-items: center;
		display: flex;
		flex-flow: column;
		font-size: 0.75em;
		justify-content: center;
		color: #b2b2b2;
		padding: 10px;
		h4 {
			font-family: fantasy, tahoma, sans-serif;
			font-size: 1.5rem;
			line-height: 2rem;
			font-weight: 500;
		}
		h5 {
			font-size: 1rem;
			font-size: 0.7rem;
			font-weight: 400;
			letter-spacing: 2px;
			text-transform: uppercase;
		}
		h6 {
			font-family: 'Roboto', sans-serif;
			font-size: 0.75rem;
			font-weight: 400;
			letter-spacing: 1px;
			text-transform: uppercase;
		}
	}
	&-header {
		align-items: center;
		display: flex;
		flex-flow: row;
		justify-content: center;
		& h2 {
			color: $pri;
			font-size: 1.7rem;
			font-weight: 400;
			flex-grow: 1;
			margin: 0 0 10px 20px;
		}
		& h3 {
			color: lighten($sec, 50%);
			font-size: 0.75rem;
			font-weight: 500;
			margin: 5px 20px;
		}
		& span {
			color: lighten($sec, 15%);
			font-weight: 400;
		}
	}
	&-wrapper {
		align-items: flex-start;
		background: #fafafa;
		border: 0;
		border-bottom: 2px solid #23548a;
		border-left: 2px solid #23548a;
		border-right: 2px solid #23548a;
		border-top: 2px solid #23548a;
		display: flex;
		flex-flow: column;
		justify-content: center;
	}
}

.blog_screen {
	align-items: center;
	background: $white;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	width: 100vw;
	&-hero {
		align-items: flex-end;
		background: lighten($color: $pri, $amount: 35%);
		display: flex;
		height: 60vh;
		position: relative;
		left: 0;
		top: 0;
		width: 100%;
		& img {
			filter: grayscale(0.75);
			height: 60vh;
			object-fit: cover;
			object-position: 50% 30%;
			overflow: hidden;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			z-index: 100;
		}
	}
	&-categories {
		display: flex;
		// justify-content: center;
		margin: 2.5rem 0 3rem 0;
		overflow: scroll;
		outline: none;
		width: 95%;
		&::-webkit-scrollbar {
			width: 8px;
			height: 8px;
			// display: none;
		}
		& ul {
			align-items: flex-start;
			display: flex;
			flex-flow: row;
			justify-content: center;
			& li {
				align-items: center;
				background: $pri;
				border-radius: 36px;
				color: $white;
				cursor: pointer;
				display: flex;
				justify-content: center;
				margin: 0 5px;
				padding: 10px;
				width: 10rem;
				&:hover {
					background: lighten($pri, 10%);
				}
			}
		}
	}
	&-container {
		align-items: flex-start;
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		gap: 20px;
		margin-bottom: 40px;
		width: 90%;
	}
	&-content {
		flex-grow: 1;
		min-width: 320px;
		width: min-content;
	}
	&-paragraph {
		color: lighten($sec, 5%);
		font-family: 'Roboto', sans-serif;
		font-size: 1.1rem;
		font-weight: 400;
		margin-bottom: 1rem;
		letter-spacing: 1px;
		padding: 20px;
		text-indent: 35px;
	}
	&-overlay {
		align-items: center;
		background: rgba($pri, 0.5);
		border-bottom: 1px solid $pri;
		border-top: 1px solid $pri;
		display: flex;
		flex-flow: column nowrap;
		margin-bottom: 20px;
		position: relative;
		width: 100%;
		z-index: 101;
	}
	&-hero_wrapper {
		align-items: flex-end;
		display: flex;
		flex-flow: column nowrap;
		height: 100%;
		justify-content: center;
		width: 80%;
		& h3 {
			color: $white;
			font-family: 'Montserrat', sans-serif;
			font-size: 2rem;
			padding: 10px;
			text-align: right;
		}
	}
}

.blog_entries {
	flex: 1 1 auto;
	margin-top: 30px;
	overflow-y: scroll;
	&::-webkit-scrollbar {
		width: 8px; /* Tamaño del scroll en vertical */
		height: 8px; /* Tamaño del scroll en horizontal */
		// display: none; /* Ocultar scroll */
	}
}

.blog_entry {
	background: $white;
	border-radius: 4px;
	cursor: pointer;
	display: flex;
	margin: 10px 5px;
	max-height: 280px;
	overflow-y: scroll;

	&-body {
		padding: 10px;
		color: $sec;
	}
	&-content {
		font-size: 10px;
	}
	&-date {
		align-items: center;
		color: $sec;
		display: flex;
		flex-flow: column;
		font-size: 10px;
		justify-content: center;
		padding: 5px;
	}
	&-picture {
		object-fit: contain;
		align-self: baseline;
		height: 100px;
		object-fit: cover;
		width: 65px;
	}
	&-title {
		font-size: 14px;
		font-weight: bold;
	}
}

.notes {
	&-main {
		display: flex;
		flex-flow: column;
		height: 95vh;
		width: 100%;
	}
	&-appbar {
		align-items: center;
		background: $pri;
		color: $white;
		display: flex;
		justify-content: space-between;
		padding: 10px 20px;
		& span {
			font-size: 12px;
		}
		& i {
			margin-right: 3px;
		}
	}
	&-content {
		background: white;
		display: flex;
		flex-direction: column;
		height: 100%;
		padding: 20px;
	}
	&-image {
		& img {
			box-shadow: 3px 3px 9px $sec;
			width: 300px;
			object-fit: cover;
		}
	}
	&-textarea {
		border: none;
		color: lighten($sec, 15%);
		display: flex;
		flex-grow: 1;
		font-size: 20px;
		height: 50vh;
		resize: none;
	}
	&-title_input {
		border: none;
		color: lighten($sec, 15%);
		// color: $sec;
		font-size: 25px;
		font-weight: 700;
		margin-bottom: 10px;
	}
}
